import axios from 'axios'
const ENV = 'PROD'
const instance = axios.create({
    baseURL: (ENV === 'PROD' ? 'https://personel.herokuapp.com' : 'http://localhost:8080') + '/api/pwa/', 
     // 'https://personel.herokuapp.com/api/pwa/',
    headers: {
        common: { 'Authorization': 'Bearer ' + localStorage.getItem('token')}
    } 
})

export default instance


